AA,CACA,kBAAA,CACA,mBAAA,CACA,UAAA,CAEA,4DACE,YAAA,CACA,iBAAA,CAEA,UAAA,CACA,cAAA,CACA,uBAAA,CAEA,WAAA,CAGF,gEACE,YAAA,CACA,kBAAA,CACA,kBAAA,CACA,WAAA,CACA,2BAAA,CACA,yBAAA,CACA,8DAAA,CAEA,sEACE,4BAAA,CACA,UAAA,CACA,UAAA,CACA,iBAAA,CAAA","sourcesContent":["/**\n * Import all the fonts used in this project.\n */\n\n@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@200;300;400&family=Overpass+Mono:wght@300;400;600;700&display=swap');\n@import url('https://use.typekit.net/zhb6cru.css');\n@import url('https://fonts.googleapis.com/css2?family=Overpass:ital@0;1&display=swap');\n\n@font-face {\n    font-family: 'Sprat Type Condensed';\n    src: url('../../assets/fonts/sprat-type/Sprat-CondensedLight.otf');\n    font-weight: 400;\n}\n\n@font-face {\n    font-family: 'Sprat Type Condensed';\n    src: url('../../assets/fonts/sprat-type/Sprat-CondensedRegular.otf');\n    font-weight: 600;\n}\n\n// Font families\n$FONT_DISPLAY: 'Sprat Type Condensed';\n$FONT_SUBHEAD: 'Overpass Mono', monospace;\n$FONT_BODY: 'franklin-gothic-urw', sans-serif;;\n\n// Font sizes\n$FONT_SIZE_NAV: 2em; // changed from 38px\n$FONT_SIZE_LINK: 2vh; //changed from 24px\n$FONT_SIZE_LINE: 12vh; // changed from 114px\n$FONT_SIZE_DESIGNER: 3vh; //changed from 36px\n$FONT_SIZE_TITLE: 48px;\n$FONT_SIZE_BODY: 18px;\n$FONT_SIZE_LABEL: 2vh; //matches font_size_link\n$FONT_SIZE_LINE_PAGE_NAME: 12vh; //changed from 108px\n\n// Font adjustments and weights\n$FONT_SPRAT_KERN: -0.1em;\n$FONT_SPRAT_WEIGHT: 200;\n$FONT_TITLE_WEIGHT: 400;\n$FONT_LINK_WEIGHT: 400;\n$FONT_LABEL_WEIGHT: 400;","/**\n * Styles shared between mobile and desktop\n */\n\n.logo {\n  fill: white;\n}\n\nbody {\n  margin: 0;\n  background-color: $LANDING_PAGE_BACKGROUND_COLOR;\n  font-family: 'Sprat Type Condensed';\n}\n\na {\n  color: white;\n}","/**\n * Constants for stylesheet\n */\n\n// sizing\n$X_MARGIN: 10vw;\n$BOTTOM_MARGIN: 10vh;\n$TOP_MARGIN: 36px;\n$DESKTOP_WIDTH: 768px;\n\n$TOP_MARGIN_MOBILE: 15px;\n\n/**\n * NOTE: this is completely arbitrary and loosely based upon values at\n * https://www.kylejlarson.com/blog/ipad-screen-size-guide-web-design-tips/.\n * - Desktop readjusted to 1240 since the navbar starts looking a bit squished\n * - Tablet set to 720 to be below minimum iPad browser width\n */\n$TABLET_WIDTH: 672px;\n$LINE_ELEMENT_HEIGHT: 142px;\n$NAV_ELEMENT_HEIGHT: 126px;\n\n// letter spacing\n$HEADER_TRACKING: -0.11em;\n$TITLE_TRACKING: -5px;\n$SUBHEAD_TRACKING: -2px;\n\n// Word spacing\n$HEADER_WORD_SPACING: 0.1em;\n\n// Background color\n$LANDING_PAGE_BACKGROUND_COLOR: #000000;\n$BACKGROUND_COLOR: #000000;\n\n// Keep track of z-indices\n$Z_INDEX_HEADER: 10;\n$Z_INDEX_MAIN_UI: 3;\n$Z_INDEX_BACKGROUND: -1;\n\n// Animation durations\n$SHORT_ANIMATION_DURATION: 200ms;\n$MEDIUM_ANIMATION_DURATION: 300ms;\n$LONG_ANIMATION_DURATION: 1000ms;","/**\n * Compiles all the styles together\n */\n\n// Shared all screens\n@import './constants.scss';\n@import './fonts.scss';\n@import './common.scss';\n\n// Desktop styles\n@media (min-width: $DESKTOP_WIDTH) {\n  @import './desktop/animations/common_animations.scss';\n  @import './desktop/desktop_constants.scss';\n  @import './desktop/index.scss';\n  @import './desktop/components/dots_lines.scss';\n  @import './desktop/components/landing_page.scss';\n  @import './desktop/components/about_page.scss';\n  @import './desktop/components/watch_page.scss';\n  @import './desktop/components/navbar.scss';\n  @import './desktop/components/navbar_line_page.scss';\n  @import './desktop/components/desktop_side_nav.scss';\n  @import './desktop/components/landing_page_prompt.scss';\n  @import './desktop/pages/line_page.scss';\n  @import './desktop/components/people_page.scss';\n  @import './desktop/animations/navbar_animations.scss';\n  @import './desktop/animations/vertical_line_animations.scss';\n  @import './desktop/animations/prompt_animations.scss';\n}\n\n// Mobile styles\n@media (max-width: $DESKTOP_WIDTH - 1) {\n  @import './mobile/mobile_constants.scss';\n  @import './mobile/index.scss';\n  @import './mobile/components/landing_page.scss';\n  @import './mobile/pages/line_page.scss';\n  @import './mobile/components/about_page.scss';\n  @import './mobile/components/dots_lines.scss';\n  @import './mobile/components/mobile_open_menu.scss';\n  @import './mobile/components/mobile_menu_line_list.scss';\n  @import './mobile/components/mobile_menu_nav_list.scss';\n}","/**\n * Common animations shared among many elements.\n *\n * For each animation, we provide some constants, and also a class that an\n * element can add so it can trigger the animation.\n */\n\n$ANIMATION_DURATION_BLUR: 500ms;\n@keyframes blur-in {\n  0% {\n    opacity: 0;\n    filter: blur(5px);\n  }\n  10% {\n    opacity: 0.75;\n  }\n  100% {\n    opacity: 1;\n    filter: blur(0);\n  }\n}\n\n.blur-in-animation {\n  animation: blur-in $ANIMATION_DURATION_BLUR linear forwards;\n}\n\n$ANIMATION_DURATION_FADE_OUT: 500ms;\n@keyframes fade-out {\n  from {\n    opacity: 1;\n  }\n\n  to {\n    opacity: 0;\n  }\n}\n\n.fade-out-animation {\n  animation: fade-out $ANIMATION_DURATION_FADE_OUT linear forwards;\n}\n\n$ANIMATION_DURATION_SLIDE_IN: 500ms;\n@keyframes right-bar-slide-in {\n  from {\n    transform: translateX(100%);\n  }\n\n  to {\n    transform: translateX(0);\n  }\n}\n\n@keyframes left-bar-slide-in {\n  from {\n    transform: translateX(-100%);\n  }\n\n  to {\n    transform: translateX(0);\n  }\n}","/**\n * Styles for the main site for DESKTOP\n */\n\n// lines and accents\n$CIRCLE_RAD : 12px;\n$OUTER : $X_MARGIN/8; // outer margin for vertical lines\n$LOWER : $BOTTOM_MARGIN/4;\n$UPPER : $BOTTOM_MARGIN*2.2;\n\nbody {\n  color: white;\n  height: 100vh;\n\twidth: 100%;\n\t-ms-overflow-style: none;  /* IE and Edge */\n\tscrollbar-width: none;  /* Firefox */\n\n\t&::-webkit-scrollbar {\n\t\tdisplay: none;\n\t}\n}\n\n.mobile {\n  display: none;\n}\n\n.main-page {\n\tdisplay: flex;\n\tflex-direction: column;\n\talign-items: center;\n\tjustify-content: center;\n}\n\n/* Display font */\nh1{\n  font-family: 'Sprat Type Condensed';\n  font-weight: 100;\n\tfont-size: 4em;\n\tletter-spacing: -.11em;\n\tword-spacing: .1em;\n\tmargin-bottom: 0;\n\ttext-transform: uppercase;\n}\n\n/* Subheader 1 */\nh2 {\n  font-family: 'Sprat Type Condensed';\n  font-weight: normal;\n\tfont-size: 1.5em;\n\tletter-spacing: -.01em;\n\tmargin-top: 0;\n\tmargin-bottom: 1em;\n\ttext-transform: uppercase;\n}\n\n/* Subheader 2 */\nh3 {\n\tfont-family: 'Overpass Mono', monospace;\n\tfont-weight: 400;\n\tfont-size: 1em;\n\tletter-spacing: -0.05em;\n\tmargin-bottom: 0;\n\tmargin-top: 0;\n\ttext-transform: uppercase;\n}\n\n/* Body text */\np {\n\tfont-family: 'franklin-gothic-urw', sans-serif;\n\tfont-weight: 400;\n\tfont-style: normal;\n\tfont-size: 1em;\n\tmargin-top: 0.5em;\n}\n\n// Common elements\n.title-theme {\n\tposition: absolute;\n\tz-index: $Z_INDEX_HEADER;\n\ttop: $TOP_MARGIN;\n\tfont-size: $FONT_SIZE_TITLE;\n\tfont-weight: $FONT_TITLE_WEIGHT;\n\tletter-spacing: $TITLE_TRACKING;\n\topacity: 0;\n\ttransition-duration: 1s;\n\tcursor: pointer;\n\n\t// Title is not visible when no line is selected\n\t&.start-animation {\n\t\tanimation: blur-in $ANIMATION_DURATION_BLUR forwards;\n\t}\n\t&.visible {\n\t\topacity: 1;\n\t}\n}","/**\n * Style definitions for overlayed lines and dots.\n */\n\n// TODO: this dot is poorly defined because it has too many specific variables,\n// making it hard to extend and use in general. See .dot-basic.\n.dot {\n  width: $CIRCLE_RAD;\n  height: $CIRCLE_RAD;\n  background-color: white;\n  border-radius: 100%;\n  position: absolute;\n  margin-bottom: -$CIRCLE_RAD/2;\n}\n\n.dot-basic {\n  width: $CIRCLE_RAD;\n  height: $CIRCLE_RAD;\n  background-color: white;\n  border-radius: 100%;\n}\n\n.line {\n  height: 1px;\n  background-color: white;\n}\n\n.vertical-line {\n  height: 100vh;\n  top: 0;\n  position: absolute;\n  border-left: 1px solid white;\n  border-right: 1px solid white;\n  pointer-events: none;\n  opacity: 0;\n}\n\n#outer-lines {\n  left: $OUTER;\n  right: $OUTER;\n}\n\n#inner-lines {\n  left: $X_MARGIN/4;\n  right: $X_MARGIN/4;\n}\n\n.lower {\n  bottom: 0;\n}\n\n.upper {\n  top: -$CIRCLE_RAD/2;\n}\n\n.left-dot {\n  left: $OUTER;\n  margin-left: -$CIRCLE_RAD/2;\n}\n\n.right-dot {\n  right: $OUTER;\n  margin-right: -$CIRCLE_RAD/2;\n}","/**\n * First page the user sees, the landing page.\n *\n * Contains a 3D asset in the back.\n */\n\n$DESIGNERS_NAME_HEIGHT: 46px;\n$SEE_MORE_WRAPPER_MARGIN: 1.25vw;\n$ARROW_WIDTH: 14px;\n$SEE_MORE_BOTTOM_MARGIN: 12px;\n\n.landing-page-background {\n  background-color: black;\n  width: 100%;\n  height: 100vh;\n  opacity: 0;\n  transition-duration: $LONG_ANIMATION_DURATION;\n\n  &.desktop-landing-page-load {\n    opacity: 0;\n  }\n\n  &.desktop-people-page-open {\n    transition-duration: 0s;\n  }\n\n  &.visible {\n    opacity: 1;\n  }\n\n  #landing-page-cube {\n    width: 100%;\n    height: 100vh;\n  }\n}\n\n#landing-page {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  overflow: hidden;\n\n  &.desktop-about-page-open {\n    .landing-page-background {\n      opacity: 0.65;\n      transition-duration: 1s;\n    }\n\n    #main-screen #curr-line {\n      opacity: 0;\n    }\n  }\n  &.desktop-watch-page-open {\n    .landing-page-background {\n      opacity: 0.65;\n      transition-duration: 1s;\n    }\n\n    #main-screen #curr-line {\n      opacity: 0;\n    }\n  }\n\n  .faded {\n    opacity: 0;\n    transition: 0.2s;\n  }\n\n  .notFaded {\n    opacity: 1;\n    transition: 0.2s;\n  }\n\n  #main-screen {\n    flex-direction: row;\n\n    &.desktop-landing-page-load {\n      opacity: 0;\n    }\n\n    #curr-line {\n      position: absolute;\n      left: $X_MARGIN/2;\n      width: 100vw - $X_MARGIN/2;\n      bottom: 40vh;\n      display: block;\n\n      #line-name {\n        font-size: $FONT_SIZE_LINE;\n        text-transform: uppercase;\n        font-weight: $FONT_SPRAT_WEIGHT;\n        letter-spacing: $HEADER_TRACKING;\n        word-spacing: $HEADER_WORD_SPACING;\n      }\n\n      #below-line-name {\n        display: flex;\n        flex-direction: row;\n\n        #designers-name {\n          display: flex;\n          height: $DESIGNERS_NAME_HEIGHT;\n          font-size: $FONT_SIZE_DESIGNER;\n          font-family: $FONT_SUBHEAD;\n          text-transform: uppercase;\n          letter-spacing: $SUBHEAD_TRACKING;\n        }\n\n        #see-more-wrapper {\n          display: flex;\n          flex-grow: 1;\n          flex-direction: column;\n          align-items: flex-end;\n          // TODO: So I think the bug here is that on windows, the font has\n          // extra padding on the bottom, and on mac, the padding appears above\n          // the font. This means the padding has to be in different spots\n          // depending on platform... I think less padding is more correct, but\n          // idk why it doesn't work.\n          // padding-bottom: $SEE_MORE_BOTTOM_MARGIN;\n          margin-left: 24px;\n          right: $SEE_MORE_WRAPPER_MARGIN;\n\n          opacity: 0;\n          transition-duration: 1s;\n          transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\n\n          &.show {\n            opacity: 1;\n          }\n\n          #more-info {\n            position: absolute;\n            display: flex;\n            flex-direction: row;\n            margin-right: $X_MARGIN/2 - $SEE_MORE_WRAPPER_MARGIN;\n            bottom: $FONT_SIZE_DESIGNER;\n            text-decoration: none;\n            cursor: pointer;\n\n            #more-info-text {\n              font-family: $FONT_SUBHEAD;\n              text-transform: uppercase;\n              font-size: $FONT_SIZE_LABEL;\n              font-weight: $FONT_LABEL_WEIGHT;\n              // border-bottom: 2px solid white;\n              margin-right: 8px;\n            }\n\n            #arrow {\n              width: $ARROW_WIDTH;\n              height: $ARROW_WIDTH;\n              margin-top: $ARROW_WIDTH - 3px;\n              border-right: 2px solid white;\n              border-bottom: 2px solid white;\n              transform: rotate(-45deg);\n            }\n          }\n\n          #see-more-line-wrapper {\n            display: flex;\n            flex-direction: row;\n            align-items: center;\n            flex-grow: 1;\n            margin-right: $SEE_MORE_WRAPPER_MARGIN;\n            // Give a little space between the designer name and the circle\n            width: 99%;\n            // The font has some bottom spacing built-in, this centers the line\n            padding-top: 5px;\n            // By default, the line is not visible\n            transform: translateX(100%);\n            transition-duration: 500ms;\n            transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\n\n            &.visible {\n              transform: translateX(0);\n            }\n\n            #see-more-line {\n              display: flex;\n              right: $OUTER;\n              border-bottom: 1px solid white;\n              flex-grow: 1;\n              height: 1px;\n              top: 1px;\n            }\n\n            // A bit different from other dots, so not going to use their styles\n            #see-more-dot {\n              bottom: $SEE_MORE_BOTTOM_MARGIN;\n              width: $CIRCLE_RAD;\n              height: $CIRCLE_RAD;\n              background-color: white;\n              border-radius: 100%;\n            }\n          }\n        }\n      }\n    }\n  }\n\n  .logo {\n    position: absolute;\n    z-index: $Z_INDEX_HEADER;\n\n    // Gonna be totally honest I don't think this is _quite_ accurate to get 21px under the logo\n    top: $TOP_MARGIN;\n    right: $X_MARGIN/2;\n    width: 5.5vh;\n    height: 7.3vh;\n    cursor: pointer;\n    transition-duration: 1s;\n\n    &.start-animation {\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\n    }\n    &.desktop-landing-page-load, .desktop-landing-page-cube-intro {\n      opacity: 0;\n    }\n    &.desktop-about-page-load, &.desktop-watch-page-load, &.desktop-people-page-load,\n    &.desktop-about-page-open, &.desktop-watch-page-open, &.desktop-people-page-open {\n      opacity: 0;\n    }\n  }\n}\n","/**\n * About page for desktop\n */\n\n$BOTTOM_DESCRIPTOR_LINE_WIDTH: 38.3vw;\n\n@keyframes expand-border-height-first {\n  0% {\n    width: 0;\n    height: 0;\n  }\n\n  50% {\n    width: 0;\n    height: 100%;\n  }\n\n  100% {\n    width: 100%;\n    height: 100%;\n  }\n}\n\n@keyframes expand-border-width-first {\n  0% {\n    width: 0;\n    height: 0;\n  }\n\n  50% {\n    width: 100%;\n    height: 0;\n  }\n\n  100% {\n    width: 100%;\n    height: 100%;\n  }\n}\n\n\n#about-page-desktop {\n  position: absolute;\n  display: flex;\n  flex-direction: column;\n  width: 100vw;\n  height: 100vh;\n  opacity: 0;\n\n  &.desktop-about-page-open {\n    opacity: 1;\n\n    // Text blurs in after all sliding animations are done\n    #headline #headline-text, #description-text {\n      opacity: 0;\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\n      animation-delay: $ANIMATION_DURATION_SLIDE_IN + $ANIMATION_DURATION_SLIDE_IN;\n    }\n\n    #right-bar {\n      animation: right-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\n    }\n\n    #bottom-descriptor {\n      .dot-basic,\n      .line {\n        animation: left-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\n      }\n\n      #description {\n        #border-left-bottom {\n          position: absolute;\n          top: 0;\n          left: 0;\n          border-bottom: 1px solid white; // Make borders visible\n          border-left: 1px solid white; // Make borders visible\n          animation: expand-border-height-first $ANIMATION_DURATION_SLIDE_IN forwards;\n          animation-delay: $ANIMATION_DURATION_SLIDE_IN;\n        }\n\n        // And this the bottom & left borders (expands left, then up)\n        #border-top-right {\n          position: absolute;\n          top: 0;\n          left: 0;\n          border-top: 1px solid white; // Make borders visible\n          border-right: 1px solid white; // Make borders visible\n          animation: expand-border-width-first $ANIMATION_DURATION_SLIDE_IN forwards;\n          animation-delay: $ANIMATION_DURATION_SLIDE_IN;\n        }\n      }\n    }\n  }\n\n  #headline {\n    display: flex;\n    flex-direction: row;\n    margin-top: 20vh;\n\n    #headline-text {\n      margin-left: $X_MARGIN/2;\n      font-family: 'Sprat Type Condensed';\n      font-weight: 100;\n      letter-spacing: -0.12em;\n      font-size: 7vw;\n      width: 75vw;\n      word-spacing: 0.1em;\n    }\n\n    #right-bar {\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      flex-grow: 1;\n\n      .dot-basic {\n        display: flex;\n      }\n\n      .line {\n        display: flex;\n        flex-grow: 1;\n      }\n    }\n  }\n\n  #bottom-descriptor {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    position: absolute;\n    bottom: 7vh;\n    width: 100vw;\n\n    #left-bar {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      justify-content: center;\n      min-width: $BOTTOM_DESCRIPTOR_LINE_WIDTH;\n\n      .dot-basic {\n        display: flex;\n      }\n\n      .line {\n        position: absolute;\n        width: $BOTTOM_DESCRIPTOR_LINE_WIDTH;\n      }\n    }\n\n    #description {\n      position: relative;\n      padding-top: 48px;\n      padding-bottom: 48px;\n      padding-left: 65px;\n      padding-right: 45px;\n      margin-right: $X_MARGIN*3/8;\n      z-index: $Z_INDEX_HEADER;\n\n      #description-text {\n        font-family: $FONT_BODY;\n        font-size: 18px;\n      }\n    }\n  }\n}","/**\n * Watch page for desktop. Similar to the about page where it is not a\n * standalone page, and lives inside the landing page. Its visibility is\n * controlled by the landing page state.\n */\n\n$MARGIN_HORIZONTAL_COUNTDOWN: 10vw;\n\n#watch-page-desktop {\n  position: absolute;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 100vw;\n  height: 100vh;\n  opacity: 0;\n\n  &.desktop-watch-page-open {\n    opacity: 1;\n\n    .right-bar {\n      animation: right-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\n    }\n\n    .left-bar {\n      animation: left-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\n    }\n\n    #countdown, #show-date-text, #show-time-text {\n      opacity: 0;\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\n      animation-delay: $ANIMATION_DURATION_SLIDE_IN;\n    }\n  }\n\n  #countdown {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    width: 100vw - 2*$MARGIN_HORIZONTAL_COUNTDOWN;\n    margin-right: $MARGIN_HORIZONTAL_COUNTDOWN;\n    margin-left: $MARGIN_HORIZONTAL_COUNTDOWN;\n\n    .countdown-component {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      width: 25%;\n\n      .number {\n        font-size: 14.4vw;\n        font-weight: 300;\n        letter-spacing: -0.12em;\n      }\n\n      .label {\n        font-family: $FONT_SUBHEAD;\n        font-size: 2.36vw;\n        font-weight: 400;\n        letter-spacing: $FONT_SPRAT_KERN;\n      }\n    }\n  }\n\n  #show-date {\n    position: absolute;\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    bottom: 8vw;\n    width: 100vw;\n\n    #show-date-text {\n      font-size: 4px;\n      font-weight: 300;\n      margin-left: $X_MARGIN*3/8;\n      margin-right: 12px;\n      backface-visibility: hidden;\n    }\n\n    .right-bar {\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      flex-grow: 1;\n\n      .line {\n        flex-grow: 1;\n        background-color: white;\n        height: 1px;\n      }\n    }\n  }\n\n  #show-time {\n    position: absolute;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    bottom: 3vw;\n    width: 100vw;\n\n    #show-time-text {\n      font-size: 4.44vw;\n      font-weight: 300;\n      letter-spacing: -0.12em;\n      margin-right: $X_MARGIN*3/8;\n      margin-left: 12px;\n      backface-visibility: hidden;\n    }\n\n    .left-bar {\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      flex-grow: 1;\n      margin-left: 12px;\n\n      .line {\n        flex-grow: 1;\n        background-color: white;\n        height: 1px;\n      }\n    }\n  }\n}","/**\n * Landing page navbar\n */\n\n$UNDERLINE_CIRCLE_WIDTH: 8px;\n$UNDERLINE_WIDTH: 32px + 2*$UNDERLINE_CIRCLE_WIDTH;\n$UNDERLINE_CIRCLE_TRANSLATE_X: $UNDERLINE_WIDTH/2;\n\n@keyframes underline-animation {\n  from {\n    width: 0px;\n  } to {\n    width: $UNDERLINE_WIDTH;\n  }\n}\n\n@keyframes underline-circle-left-animation {\n  from {\n    transform: translateX(0);\n  } to {\n    transform: translateX(-$UNDERLINE_CIRCLE_TRANSLATE_X);\n  }\n}\n\n@keyframes underline-circle-right-animation {\n  from {\n    transform: translateX(0);\n  } to {\n    transform: translateX($UNDERLINE_CIRCLE_TRANSLATE_X);\n  }\n}\n\n.navbar {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 100vw;\n  // Put this navbar at the bottom\n  position: fixed;\n  bottom: $OUTER;\n\n  border-top: 1px solid white;\n  border-bottom: 1px solid white;\n  padding-top: 2.5vh;\n  padding-bottom: 2.5vh;\n  transition-duration: 1s;\n  pointer-events: none;\n\n  &.default {\n    pointer-events: all;\n    .navbar-container {\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\n    }\n  }\n  &.desktop-landing-page-load {\n    opacity: 0;\n\n    // We need to keep this because the navbar will have opacity: 1 at the end\n    // of its animation\n    .navbar-container {\n      opacity: 0;\n      pointer-events: none;;\n    }\n  }\n  &.desktop-landing-page-cube-intro {\n    .navbar-container {\n      opacity: 0;\n      pointer-events: none;;\n    }\n  }\n  &.desktop-about-page-open, &.desktop-watch-page-open, &.desktop-people-page-open {\n    opacity: 0;\n  }\n  &.desktop-about-page-load, &.desktop-watch-page-load, &.desktop-people-page-load {\n    opacity: 0;\n  }\n\n  .navbar-container {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: center;\n    width: 100vw - 2*3*$OUTER;\n    transition-duration: 1s;\n  }\n\n  .navBall {\n    margin: auto;\n    display: block;\n    transform: scale(.9, .8);\n    opacity: 0;\n    transition: .5s;\n  }\n\n  .underline-circle {\n    position: absolute;\n    width: $UNDERLINE_CIRCLE_WIDTH;\n    height: $UNDERLINE_CIRCLE_WIDTH;\n    background-color: white;\n    border-radius: 100%;\n  }\n\n  .underline {\n    position: absolute;\n    width: 0px;\n    height: 1px;\n    background-color: white;\n  }\n\n  .navbar-item {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    font-size: $FONT_SIZE_NAV;\n    cursor: pointer;\n    font-weight: $FONT_SPRAT_WEIGHT;\n    transition: .5s;\n    border-bottom: 2px solid;\n    border-color: rgba(255, 255, 255, 0);\n    padding-bottom: $BOTTOM_MARGIN*0.05;\n\n    .underline-animation {\n      margin-top: 4px;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      width: 100%;\n      height: 12px;\n      opacity: 0;\n      transition: $MEDIUM_ANIMATION_DURATION;\n    }\n\n    &.selected {\n      .navBall {\n        opacity: 1;\n      }\n\n      .underline-animation {\n        opacity: 1;\n\n        .left {\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-left-animation forwards;\n        }\n\n        .right {\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-right-animation forwards;\n        }\n\n        .underline {\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-animation forwards;\n        }\n      }\n    }\n  }\n}\n","/**\n * Line page navbar\n */\n\n$UNDERLINE_CIRCLE_WIDTH: 8px;\n$UNDERLINE_WIDTH: 32px + 2*$UNDERLINE_CIRCLE_WIDTH;\n$UNDERLINE_CIRCLE_TRANSLATE_X: $UNDERLINE_WIDTH/2;\n$TOOLTIP_CIRCLE_WIDTH: 10px;\n$TOOLTIP_CIRCLE_HEIGHT: 8px;\n$TOOLTIP_MARGIN: 10px;\n$TOOLTIP_TICK_LENGTH: 27px;\n\n@keyframes underline-animation {\n  from {\n    width: 0px;\n  } to {\n    width: $UNDERLINE_WIDTH;\n  }\n}\n\n@keyframes underline-circle-left-animation {\n  from {\n    transform: translateX(0);\n  } to {\n    transform: translateX(-$UNDERLINE_CIRCLE_TRANSLATE_X);\n  }\n}\n\n@keyframes underline-circle-right-animation {\n  from {\n    transform: translateX(0);\n  } to {\n    transform: translateX($UNDERLINE_CIRCLE_TRANSLATE_X);\n  }\n}\n\n.navbar-line-page {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 100vw;\n  margin-top: 6.66vw;\n\n  border-top: 1px solid white;\n  border-bottom: 1px solid white;\n  padding-top: 2.5vh;\n  padding-bottom: 2.5vh;\n\n  .navbar-container {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: center;\n    width: 100vw - 2*3*$OUTER;\n  }\n\n  .navBall {\n    margin: auto;\n    display: block;\n    transform: scale(.9, .8);\n    opacity: 0;\n    transition: .5s;\n  }\n\n  .underline-circle {\n    position: absolute;\n    width: $UNDERLINE_CIRCLE_WIDTH;\n    height: $UNDERLINE_CIRCLE_WIDTH;\n    background-color: white;\n    border-radius: 100%;\n  }\n\n  .underline {\n    position: absolute;\n    width: 0px;\n    height: 1px;\n    background-color: white;\n  }\n\n  .navbar-item {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    font-size: $FONT_SIZE_NAV;\n    cursor: pointer;\n    font-weight: $FONT_SPRAT_WEIGHT;\n    transition: .5s;\n    border-bottom: 2px solid;\n    border-color: rgba(255, 255, 255, 0);\n    padding-bottom: $BOTTOM_MARGIN*0.05;\n    transition: .5s;\n    text-decoration: none;\n\n    .underline-animation {\n      margin-top: 4px;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      width: 100%;\n      height: 12px;\n      opacity: 0;\n      transition: $MEDIUM_ANIMATION_DURATION;\n    }\n\n    &.selected {\n      .navBall {\n        opacity: 1;\n      }\n\n      .underline-animation {\n        opacity: 1;\n\n        .left {\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-left-animation forwards;\n        }\n\n        .right {\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-right-animation forwards;\n        }\n\n        .underline {\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-animation forwards;\n        }\n      }\n    }\n\n    &:hover .tooltip {\n      opacity: 1;\n    }\n\n    // Special margin rule for the first line since it doesn't fit\n    &#line-0 .tooltip .wrapper {\n      margin-left: 82px;\n\n      #left-top {\n        margin-left: 82px;\n      }\n    }\n\n    .tooltip {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      position: absolute;\n      bottom: 52px;\n      opacity: 0;\n      transition-duration: 200ms;\n\n      .vertical-tick {\n        width: 1px;\n        height: $TOOLTIP_TICK_LENGTH;\n        border-right: 1px solid white;\n      }\n\n      .wrapper {\n        display: flex;\n        flex-direction: row;\n        justify-content: center;\n        border: 1px solid white;\n        padding-left: 10px;\n        padding-right: 10px;\n        background-color: $LANDING_PAGE_BACKGROUND_COLOR;\n\n        .label {\n          border-left: 1px solid white;\n          border-right: 1px solid white;\n          font-family: $FONT_SUBHEAD;\n          font-weight: 400;\n          font-size: 18px;\n          text-transform: uppercase;\n          white-space: nowrap;\n\n          padding-top: 8px;\n          padding-bottom: 6px;\n          padding-left: $TOOLTIP_MARGIN;\n          padding-right: $TOOLTIP_MARGIN;\n        }\n\n        .dot-basic {\n          width: $TOOLTIP_CIRCLE_WIDTH;\n          height: $TOOLTIP_CIRCLE_HEIGHT;\n          position: absolute;\n        }\n\n        #left-top {\n          top: -$TOOLTIP_CIRCLE_WIDTH/2 + 1;\n          left: $TOOLTIP_CIRCLE_WIDTH/2 + 2;\n        }\n\n        #right-bottom {\n          right: $TOOLTIP_CIRCLE_WIDTH/2 + 1;\n          bottom: $TOOLTIP_TICK_LENGTH - $TOOLTIP_CIRCLE_WIDTH/2 + 1;\n        }\n      }\n\n      &.inactive {\n        opacity: 0;\n      }\n    }\n  }\n}\n","/**\n * Navigation sidebar on desktop\n */\n\n$DESKTOP_SIDE_NAV_SLIDE_DURATION: 400ms;\n\n@keyframes left-bar-slide-in-desktop-nav {\n  from {\n    transform: translateX(-50%);\n  }\n\n  to {\n    transform: translateX(0);\n  }\n}\n\n.links {\n  position: absolute;\n  top: $TOP_MARGIN;\n  left: 0;\n  transition-duration: 1s;\n  opacity: 0;\n\n  &.start-animation {\n    animation: blur-in $ANIMATION_DURATION_BLUR forwards;\n  }\n\n  &.desktop-landing-page-load {\n    opacity: 0;\n  }\n\n  &.desktop-landing-page-cube-intro {\n    opacity: 0;\n  }\n\n  .link-wrapper {\n    display: flex;\n    flex-direction: row;\n    margin-bottom: 4px; //changed from 8px\n    transition-duration: 1s;\n\n    &.selected .left-bar {\n      animation: left-bar-slide-in-desktop-nav $DESKTOP_SIDE_NAV_SLIDE_DURATION forwards cubic-bezier(0.075, 0.82, 0.165, 1);\n      opacity: 1;\n    }\n\n    .left-bar {\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      opacity: 0;\n      transform: translateX(-100%);\n\n      .line {\n        display: flex;\n        flex-grow: 1;\n        padding-right: $X_MARGIN/4;\n      }\n\n      .dot-basic {\n        margin-left: -$CIRCLE_RAD/2;\n        margin-right: 14px;\n      }\n    }\n\n    .link {\n      font-family: $FONT_BODY;\n      font-size: $FONT_SIZE_LINK;\n      text-transform: uppercase;\n      font-weight: $FONT_LINK_WEIGHT;\n      display: block;\n      color: white;\n      text-decoration: none;\n    }\n  }\n}","/**\n * Prompt that appears after the initial load and before a user enters the main\n * site.\n */\n\n$MARGIN_BAR: 12px;\n$BAR_WIDTH: 74vw;\n\n#landing-page-prompt {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  width: 100vw;\n  position: absolute;\n  opacity: 0;\n\n  &.hidden {\n    display: none;\n  }\n\n  .bar {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    width: 100vw;\n\n    .line {\n      display: flex;\n      flex-grow: 1;\n    }\n\n    .hidden {\n      width: 100vw - $BAR_WIDTH;\n    }\n  }\n\n  .right-bar {\n    margin-bottom: $MARGIN_BAR;\n  }\n\n  .left-bar {\n    margin-top: $MARGIN_BAR;\n  }\n\n  #title {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    font-size: 14vh;\n    font-weight: 300;\n    letter-spacing: $FONT_SPRAT_KERN;\n  }\n\n  #enter-site {\n    margin-top: 12px;\n    font-family: $FONT_SUBHEAD;\n    text-transform: uppercase;\n    font-size: 2vh;\n    cursor: pointer;\n  }\n}","/**\n * Styles for the page showing each individual line\n */\n\n$LEFT_MARGIN: 25vw;\n$TITLE_MARGIN_TOP: 15vw;\n\n$SLIDE_AMOUNT_A: 6.66vw;\n$SLIDE_AMOUNT_B: -2.8vw;\n$SLIDE_AMOUNT_C: 6.4vw;\n$SLIDE_AMOUNT_D: 16.6vw;\n$BACK_BUTTON_SIZE: 24px;\n$BACK_BUTTON_STROKE_WIDTH: 3px;\n\n// Animations\n@keyframes slide-image-a {\n  from {\n    transform: translateY(0);\n  } to {\n    transform: translateY($SLIDE_AMOUNT_A);\n  }\n}\n\n@keyframes slide-image-b {\n  from {\n    transform: translateY(0);\n  } to {\n    transform: translateY($SLIDE_AMOUNT_B);\n  }\n}\n\n@keyframes slide-image-c {\n  from {\n    transform: translateY(0);\n  } to {\n    transform: translateY($SLIDE_AMOUNT_C);\n  }\n}\n\n@keyframes slide-image-d {\n  from {\n    transform: translateY(0);\n  } to {\n    transform: translateY($SLIDE_AMOUNT_D);\n  }\n}\n\n#line-page {\n  width: 100vw;\n  padding-bottom: 2.5vh;\n\n  #background {\n    display: flex;\n    flex-direction: column;\n    align-items: flex-end;\n    position: absolute;\n    top: 0;\n    z-index: -1;\n\n    // TODO: did we want to do a gradual opacity fade for the background?\n    #first {\n      background-size: cover;\n      height: 56.25vw;\n      width: 100vw;\n      opacity: 0.9;\n    }\n\n    #second {\n      background-size: cover;\n      height: 46vw;\n      width: 80vw;\n      margin-top: 14px;\n      opacity: 0.6;\n    }\n  }\n\n  #top-title {\n    position: fixed;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 100vw;\n    margin-top: $TOP_MARGIN;\n    font-family: 'Sprat Type Condensed';\n    font-size: $FONT_SIZE_TITLE;\n    font-weight: 100;\n    letter-spacing: -0.05em;\n    text-decoration: none;\n  }\n\n  .main-content {\n    display: flex;\n    flex-direction: column;\n    align-items: flex-start;\n    margin-left: $LEFT_MARGIN;\n\n    #name {\n      // TODO: this is really bad style for font sizes and letter spacing. Not\n      // scalable\n      margin-top: $TITLE_MARGIN_TOP;\n      font-family: 'Sprat Type Condensed';\n      font-size: $FONT_SIZE_LINE_PAGE_NAME;\n      font-weight: 100;\n      letter-spacing: -0.12em;\n      text-transform: uppercase;\n      word-spacing: $HEADER_WORD_SPACING;\n    }\n\n    #designers {\n      display: flex;\n      flex-direction: row;\n      align-items: stretch;\n      margin-top: 10px;\n      width: 100%;\n\n      #designers-text {\n        display: flex;\n        font-family: $FONT_SUBHEAD;\n        font-weight: 400;\n        font-size: 24px;\n        text-transform: uppercase;\n      }\n\n      #right-bar {\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        flex-grow: 1;\n        margin-left: 12px;\n        margin-bottom: 4px;\n\n        .line {\n          flex-grow: 1;\n          background-color: white;\n          height: 1px;\n        }\n      }\n    }\n\n    #pictures-top {\n      margin-top: 9.4vw;\n      display: flex;\n      flex-direction: row;\n      justify-content: space-between;\n      width: 100%;\n\n      .sliding-image {\n        width: 16vw;\n        height: 23.8vw;\n\n        .frame {\n          width: calc(100% - 4px);\n          height: calc(100% - 2px);\n          border: 1px solid white;\n        }\n\n        .image {\n          width: 16vw;\n          height: 23.8vw;\n          z-index: -1;\n          position: absolute;\n          object-fit: none;\n\n          animation-delay: 1s;\n          animation-duration: 2s;\n          animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\n          animation-fill-mode: forwards;\n\n          &#a{\n            animation-name: slide-image-a;\n          }\n\n          &#b{\n            animation-name: slide-image-b;\n          }\n\n          &#c{\n            animation-name: slide-image-c;\n          }\n\n          &#d{\n            animation-name: slide-image-d;\n          }\n        }\n      }\n    }\n\n    #description {\n      width: 36vw;\n      margin-top: 12.5vw;\n      font-family: $FONT_BODY;\n      font-size: $FONT_SIZE_BODY;\n    }\n\n    #left-bar {\n      margin-top: 4.26vw;\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      flex-grow: 1;\n      margin-left: -25vw;\n\n      .dot-basic {\n        margin-left: -1px;\n      }\n\n      .line {\n        width: 60vw;\n        flex-grow: 1;\n        background-color: white;\n        height: 1px;\n      }\n    }\n\n    #pictures-bottom {\n      display: flex;\n      flex-direction: row;\n      margin-top: 2.86vw;\n\n      img {\n        height: 47vw;\n      }\n\n      #left {\n        margin-right: 3.61vw;\n        object-fit: cover;\n      }\n    }\n  }\n\n  .fixed-overlay {\n    position: fixed;\n    top: $TOP_MARGIN;\n    left: $X_MARGIN/2;\n\n    .links {\n      position: static;\n      top: 0;\n      left: 0;\n\n      .link-wrapper .left-bar {\n        display: none;\n      }\n    }\n  }\n\n  #back-button-wrapper {\n    position: fixed;\n    top: calc(#{$TITLE_MARGIN_TOP} + 40px);\n    left: $X_MARGIN/2;\n\n    #back-button {\n\n      width: $BACK_BUTTON_SIZE;\n      height: $BACK_BUTTON_SIZE;\n      border-left: $BACK_BUTTON_STROKE_WIDTH solid white;\n      border-bottom: $BACK_BUTTON_STROKE_WIDTH solid white;\n      transform: rotate(45deg);\n\n      cursor: pointer;\n\n      opacity: 0;\n      transition-duration: 1s;\n\n      &.show {\n        opacity: 1;\n      }\n    }\n  }\n}","/**\n * The people page\n */\n\n#people-page.main-page {\n  display: flex;\n  flex-direction: column;\n\talign-items: center;\n\toverflow: hidden;\n\n\n\t&.desktop-people-page-open {\n\t\t.page-background, #team-accordion {\n\t\t\tanimation: blur-in $ANIMATION_DURATION_BLUR forwards;\n      backface-visibility: hidden;\n\t\t}\n\t}\n\n\t.headshotImg {\n\t\tbackground-size: cover;\n\t\tbackground-repeat: no-repeat;\n\t\tbackground-blend-mode: color;\n\t}\n\n\t.headshot {\n\t\twidth: 20vw;\n\t\theight: 21.3vw;\n\t\tbackground-size: cover;\n\t\tbackground-repeat: no-repeat;\n\t\tbackground-blend-mode: color;\n\t}\n\n\t.page-background {\n\t\ttop: 0;\n\t\tbottom: 0;\n\t\tposition: fixed;\n\t\theight: 100vh;\n\t\twidth: 100vw;\n\t\topacity: 0;\n\t}\n\n\t.wrapper {\n\t\tposition: fixed;\n\t\ttop: 0;\n\t\tleft: 0;\n\t\tright: 0;\n\t\theight: 30vh;\n\t\tbackground: linear-gradient(180deg, rgba(20,20,20,1) 40%, rgba(20,20,20,0) 100%);\n\t\tz-index: 100;\n\t\tpadding: 0;\n\n\t\t.header {\n\t\t\tpadding: 0;\n\t\t\tmargin: 0;\n\t\t\tdisplay: flex;\n\t\t\tflex-direction: column;\n\t\t\talign-items: center;\n\t\t}\n\t}\n\n  #team-accordion {\n\t\tposition: absolute;\n\t\tleft: $X_MARGIN/2;\n\t\tdisplay: block;\n\t\ttop: 25%;\n\t\tbackground: none;\n\t\tbox-shadow: none;\n\t\topacity: 0;\n\n\t\t.Mui-expanded {\n\t\t\tmargin: 0;\n\t\t\tpadding: 0;\n\t\t}\n\n\t\t.team-title {\n\t\t\tfont-family: $FONT_DISPLAY;\n\t\t\ttext-transform: uppercase;\n\t\t\tletter-spacing: $FONT_SPRAT_KERN;\n\t\t\tfont-weight: $FONT_SPRAT_WEIGHT;\n\t\t\tfont-size: 10vh; //May need to