n  }\n  100% {\n    transform: translateX(0);\n  }\n}\n@keyframes navbar-right-dots {\n  0% {\n    transform: translateX($X_MARGIN/8);\n    opacity: 0;\n  }\n  10% {\n    opacity: 1;\n  }\n  100% {\n    transform: translateX(0);\n  }\n}\n@keyframes navbar-expand {\n  0% {\n    width: 2*$CIRCLE_RAD;\n    opacity: 0;\n  }\n  10% {\n    opacity: 1;\n  }\n  100% {\n    width: 100vw;\n  }\n}\n\n.navbar {\n  &.start-animation {\n    opacity: 1;\n    animation-duration: $ANIMATION_DURATION_NAVBAR;\n    animation-timing-function: $ANIMATION_TIMING_NAVBAR;\n    animation-name: navbar-expand;\n    animation-fill-mode: forwards;\n\n    .left-dot {\n      animation-duration: $ANIMATION_DURATION_NAVBAR;\n      animation-timing-function: $ANIMATION_TIMING_NAVBAR;\n      animation-name: navbar-left-dots;\n      animation-fill-mode: forwards;\n    }\n\n    .right-dot {\n      animation-duration: $ANIMATION_DURATION_NAVBAR;\n      animation-timing-function: $ANIMATION_TIMING_NAVBAR;\n      animation-name: navbar-right-dots;\n      animation-fill-mode: forwards;\n    }\n  }\n}","$ANIMATION_DURATION_VERTICAL_LINE: 1s;\n$ANIMATION_TIMING_VERTICAL_LINE: linear;\n\n@keyframes come-down-from-top {\n  from {\n    transform: translateY(-100vh);\n  } to {\n    transform: translateY(0);\n  }\n}\n\n.vertical-line {\n  &.start-animation {\n    opacity: 1;\n    animation-name: come-down-from-top;\n    animation-duration: $ANIMATION_DURATION_VERTICAL_LINE;\n    animation-timing-function: $ANIMATION_TIMING_VERTICAL_LINE;\n    animation-fill-mode: forwards;\n  }\n}","/**\n * Animations for when the page first loads.\n */\n\n$ANIMATION_DURATION_PROMPT: 1s;\n$ANIMATION_TIMING_PROMPT: linear;\n\n@keyframes prompt-title-blur-out {\n  from {\n    opacity: 1;\n    filter: blur(0);\n  }\n  to {\n    opacity: 0;\n    filter: blur(2rem);\n  }\n}\n@keyframes prompt-title-blur-in {\n  0% {\n    opacity: 0;\n    filter: blur(2rem);\n  }\n  75% {\n    opacity: 1;\n    filter: blur(0);\n  }\n  100% {\n    opacity: 1;\n    filter: blur(0);\n  }\n}\n\n@keyframes right-bar-slide-out {\n  0% {\n    transform: translateX(0);\n  }\n  50% {\n    transform: translateX(0);\n  }\n  100% {\n    transform: translateX(100vw);\n  }\n}\n@keyframes right-bar-slide-in {\n  0% {\n    transform: translateX(100vw);\n  }\n  50% {\n    transform: translateX(100vw);\n  }\n  100% {\n    transform: translateX(0);\n  }\n}\n\n@keyframes left-bar-slide-out {\n  0% {\n    transform: translateX(0);\n  }\n  50% {\n    transform: translateX(0);\n  }\n  100% {\n    transform: translateX(-100vw);\n  }\n}\n@keyframes left-bar-slide-in {\n  0% {\n    transform: translateX(-100vw);\n  }\n  50% {\n    transform: translateX(-100vw);\n  }\n  100% {\n    transform: translateX(0);\n  }\n}\n\n// The enter button appears at the end\n@keyframes enter-site-fade-out {\n  0% {\n    opacity: 1;\n  }\n  80% {\n    opacity: 0;\n  }\n  100% {\n    opacity: 0;\n  }\n}\n@keyframes enter-site-fade-in {\n  0% {\n    opacity: 0;\n  }\n  80% {\n    opacity: 0;\n  }\n  100% {\n    opacity: 1;\n  }\n}\n\n#landing-page-prompt {\n  &.start-animation {\n    opacity: 1;\n\n    #title {\n      animation-duration: $ANIMATION_DURATION_PROMPT;\n      animation-timing-function: $ANIMATION_TIMING_PROMPT;\n      animation-fill-mode: forwards;\n      animation-name: prompt-title-blur-in;\n    }\n\n    .left-bar {\n      animation-duration: $ANIMATION_DURATION_PROMPT;\n      animation-timing-function: $ANIMATION_TIMING_PROMPT;\n      animation-fill-mode: forwards;\n      animation-name: left-bar-slide-in;\n    }\n\n    .right-bar {\n      animation-duration: $ANIMATION_DURATION_PROMPT;\n      animation-timing-function: $ANIMATION_TIMING_PROMPT;\n      animation-fill-mode: forwards;\n      animation-name: right-bar-slide-in;\n    }\n\n    #enter-site {\n      animation-duration: $ANIMATION_DURATION_PROMPT;\n      animation-timing-function: $ANIMATION_TIMING_PROMPT;\n      animation-fill-mode: forwards;\n      animation-name: enter-site-fade-in;\n    }\n  }\n\n  &.out-animation {\n    opacity: 1;\n    #title {\n      animation-duration: $ANIMATION_DURATION_PROMPT;\n      animation-timing-function: $ANIMATION_TIMING_PROMPT;\n      animation-fill-mode: forwards;\n      animation-name: prompt-title-blur-out;\n    }\n\n    .left-bar {\n      animation-duration: $ANIMATION_DURATION_PROMPT;\n      animation-timing-function: $ANIMATION_TIMING_PROMPT;\n      animation-fill-mode: forwards;\n      animation-name: left-bar-slide-out;\n    }\n\n    .right-bar {\n      animation-duration: $ANIMATION_DURATION_PROMPT;\n      animation-timing-function: $ANIMATION_TIMING_PROMPT;\n      animation-fill-mode: forwards;\n      animation-name: right-bar-slide-out;\n    }\n\n    #enter-site {\n      animation-duration: $ANIMATION_DURATION_PROMPT;\n      animation-timing-function: $ANIMATION_TIMING_PROMPT;\n      animation-fill-mode: forwards;\n      animation-name: enter-site-fade-out;\n    }\n  }\n}","/**\n * Mobile styles in general for the site\n */\n\nhtml, body {\n  height: 100%;\n  width: 100%;\n  margin: 0;\n  padding: 0;\n\n  color: white;\n}\n\nbody #root {\n  height: 100%;\n}\n\n.desktop {\n  display: none;\n}","\n// TODO: unsure why this font is so much smaller than figma\n$FONT_SIZE_TITLE: 48px;\n\n/**\n * 3D model in the landing page.\n */\n.landing-page-background {\n  z-index: $Z_INDEX_BACKGROUND;\n  position: fixed;\n  background-color: black;\n  width: 100%;\n  height: 100%;\n  transition-duration: 1s;\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\n  opacity: 0;\n  transition-duration: $LONG_ANIMATION_DURATION;\n\n  &.visible {\n    opacity: 1;\n  }\n\n  #landing-page-cube {\n    width: 100%;\n    height: 100%;\n  }\n\n  &.mobile-nav-menu-open {\n    opacity: 0;\n  }\n\n  &.mobile-about-page-open {\n    opacity: 0.6;\n  }\n\n  &.mobile-line-menu-open {\n    opacity: 0;\n  }\n}\n\n#landing-page {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  height: 100%;\n  width: 100%;\n  overflow: hidden;\n\n  &.mobile-line-menu-open {\n    overflow-y: auto;\n    overflow-x: hidden;\n  }\n\n  .title-theme {\n    position: fixed;\n    font-size: $FONT_SIZE_TITLE;\n    // Vertically stretch the font\n    transform: scale(1, 1.5);\n    transition-duration: 1s;\n    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\n    opacity: 1;\n\n    // Title disappears on nav menu open\n    &.mobile-nav-menu-open {\n      opacity: 0;\n    }\n\n    &.mobile-about-page-open {\n      opacity: 0;\n    }\n\n    &.mobile-line-menu-open {\n      opacity: 0;\n    }\n  }\n\n  // Hide desktop elements\n  .link {\n    display: none;\n  }\n\n  .lineLabel {\n    display: none;\n  }\n\n  #label {\n    display: none;\n  }\n\n  #curr_line {\n    display: none;\n  }\n\n  .logo {\n    position: fixed;\n    bottom: 51px;\n    width: 41px;\n    height: 54px;\n\n    &.mobile-about-page-open {\n      opacity: 0;\n    }\n  }\n}\n","/**\n * Line page styles for mobile\n */\n$LEFT_MARGIN: $X_MARGIN/2;\n$RIGHT_MARGIN: $X_MARGIN/2;\n\n\n$SLIDE_IMG_WIDTH: 41.3vw;\n$SLIDE_IMG_HEIGHT: 61vw;\n$SLIDE_AMOUNT_A: 9.86vw;\n$SLIDE_AMOUNT_B: -14.38vw;\n\n// Animations\n@keyframes slide-image-a {\n  from {\n    transform: translateY(0);\n  } to {\n    transform: translateY($SLIDE_AMOUNT_A);\n  }\n}\n\n@keyframes slide-image-b {\n  from {\n    transform: translateY(0);\n  } to {\n    transform: translateY($SLIDE_AMOUNT_B);\n  }\n}\n\n#line-page {\n  width: 100vw;\n  padding-bottom: 34vw;\n\n  #background {\n    display: flex;\n    flex-direction: column;\n    position: absolute;\n    top: 0;\n    z-index: -1;\n    height: 100%;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n\n    // TODO: did we want to do a gradual opacity fade for the background?\n    #first {\n      opacity: 0.9;\n      height: 100%;\n      width: 100%;\n    }\n\n    #second {\n      display: none;\n    }\n  }\n\n  #top-title {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 100vw;\n    margin-top: $TOP_MARGIN_MOBILE;\n    font-family: 'Sprat Type Condensed';\n    font-size: 24px;\n    font-weight: 100;\n    letter-spacing: -0.05em;\n    text-decoration: none;\n  }\n\n  .main-content {\n    display: flex;\n    flex-direction: column;\n    align-items: flex-start;\n    margin-left: $LEFT_MARGIN;\n    margin-right: $RIGHT_MARGIN;\n\n    #name {\n      // TODO: this is really bad style for font sizes and letter spacing. Not\n      // scalable\n      margin-top: 38px;\n      font-family: 'Sprat Type Condensed';\n      font-size: 48px;\n      font-weight: 100;\n      letter-spacing: -0.12em;\n      text-transform: uppercase;\n      word-spacing: $HEADER_WORD_SPACING;\n    }\n\n    #designers {\n      display: flex;\n      flex-direction: row;\n      align-items: stretch;\n      margin-top: 10px;\n      width: 100%;\n\n      #designers-text {\n        display: flex;\n        font-family: $FONT_SUBHEAD;\n        font-weight: normal;\n        font-size: 20px;\n        letter-spacing: -0.05em;\n        text-transform: uppercase;\n      }\n\n      #right-bar {\n        display: none;\n      }\n    }\n\n    #pictures-top {\n      margin-top: 18.5vw;\n      display: flex;\n      flex-direction: row;\n      justify-content: space-between;\n      width: 100%;\n\n      .sliding-image {\n        width: $SLIDE_IMG_WIDTH;\n        height: $SLIDE_IMG_HEIGHT;\n\n        &#c{\n          display: none;\n        }\n\n        &#d{\n          display: none;\n        }\n\n        .frame {\n          width: calc(100% - 4px);\n          height: calc(100% - 2px);\n          border: 2px solid white;\n        }\n\n        .image {\n          width: $SLIDE_IMG_WIDTH;\n          height: $SLIDE_IMG_HEIGHT;\n          z-index: -1;\n          position: absolute;\n          object-fit: none;\n\n          animation-delay: 1s;\n          animation-duration: 2s;\n          animation-timing-function: cubic-bezier(0.075